<template>
  <div class="order_list">
    <div class="productInfo">
      <div class="clearfix">
        <div class="info fl">{{leftInfo}}</div>
        <div class="fr" v-show="isShow">
          <div class="sure"  v-if="!isReceive" @click="confirmReceive">
                  确认收货
              </div>
          <div class="com_del clearfix" v-else>
            <div class="complete fl">已完成</div>
            <div class="delete fl" @click="remove">
              <van-icon name="delete" size="19" />
            </div>
          </div>
        </div>
      </div>
      <div class="productItem">
        <slot></slot>

        <!-- 订单金额合计 -->
      </div>
      <div class="total clearfix">
      <div class="date" v-if="date != ''">{{date}}</div>
        <div class="product_count fl">共计{{count}}件商品</div>
        <div class="price_count fr">合计￥{{total}}</div>
      </div>
    </div>

    <div ></div>
  </div>
</template>

<script>
import OrderItem from "./OrderItem.vue";
export default {
  components: { OrderItem },
  name: "OrderList",
  props:{
    leftInfo:{
      type:String,
      default:'左边标题'
    },
    rightInfo:{
      type:Boolean,
      default:false
    },
    date:{
      type:String,
      default:'2020-01-02 00:20:03'
    },
    // 商品数量
    count:{
      type:Number,
      default:0
    },
     //订单总金额
    total: {
      type: [Number, String],
      default: 0
    },
     isShow:{
       type:Boolean,
       default:true
     },
     isReceive:{
       type:Boolean,
       default:true
     }
  },

  methods: {
    remove(){
      this.$emit('remove')
    },
    confirmReceive(){
      this.$emit('confirm-receive')
    },
    showRight(){
      this.$emit('show-right');
    }
  },
  
};
</script>

<style lang="less" scoped>
.productInfo {
  padding: 10px;
  background-color: #fff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  overflow: hidden;
  .info {
    font-size: 10px;
    font-weight: bold;
  }

  .sure {
    font-size: 14px;
  }
  .com_del {
    font-size: 14px;
    .delete {
      margin-left: 10px;
      width: 19px;
      height: 19px;
      margin-top: 1px;
    }
  }
  .productItem {
    padding: 10px 10px 40px 10px;
    position: relative;
  }
 
  .total {
    padding: 10px;
    border-top: 1px dashed #ddd;
    position: relative;

    .price_count {
      color: #780d00;
      font-weight: bold;
      margin-top: 10px;

    }
    .date{
      font-size: 14px;
    }
    .product_count {
      margin-top: 10px;
      font-weight: bold;
    }
  }
  .productItem::after {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #f7f7f7;
    content: "";
    position: absolute;
    bottom: -10px;
    left: -18px;
  }
  .productItem::before {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #f7f7f7;
    content: "";
    position: absolute;
    bottom: -10px;
    right: -18px;
  }
}
</style>