<template>
  <div class="order">
    <van-nav-bar
      title="我的订单"
      left-text="返回"
      left-arrow
      fixed
      @click-left="goback"
    />
    <BgBox>
      <div v-if="isHas">
        <van-tabs v-model="tabIndex" @change="changeTab">
          <van-tab
            v-for="(item, index) in nabTitle"
            :key="index"
            :title="item.title"
          >
            <div class="content" v-if="orderList.length > 0">
              <div class="item" v-for="(sub, i) in orderList" :key="i">
                <OrderList
                  :leftInfo="sub.oid"
                  :date="sub.date | formatDate('yyyy-MM-dd hh:mm:ss')"
                  :count="sub.count"
                  :total="sub.total | decimal"
                  :is-right="true"
                  @confirm-receive="confirmReceive(sub, i)"
                  @remove="removeOrder(sub.oid, i)"
                  :is-receive="sub.status == 2"
                >
                  <OrderItem
                    v-for="(tab, index) in sub.data"
                    :key="index"
                    :item="tab"
                  >
                  </OrderItem>
                </OrderList>
              </div>
            </div>
            <div v-else>
              <van-empty description="没有订单数据" />
            </div>
          </van-tab>
        </van-tabs>
      </div>
       <div v-else>
        <van-empty description="没有订单数据" />
      </div>
    </BgBox>
  </div>
</template>

<script>
import BgBox from "../components/BgBox";
import OrderList from "../components/OrderList";
import OrderItem from "../components/OrderItem";
import "../assets/less/order.less";
export default {
  name: "Order",

  data() {
    return {
      tabIndex: 0,
      nabTitle: [{ title: "全部" }, { title: "进行中" }, { title: "已完成" }],
      orderList: [],
      isHas: true,
    };
  },

  components: {
    BgBox,
    OrderList,
    OrderItem,
  },
  created() {
    this.orderData();
  },

  methods: {
    goback() {
      this.$router.go(-1);
    },

    //切换订单状态 0: 全部, 1: 进行中, 2: 已完成
    changeTab(name, title) {
      console.log("name ==> ", name);
      console.log("title ==> ", title);

      console.log("this.tabIndex ==> ", this.tabIndex);

      //根据订单状态获取订单数据
      this.orderData();
    },

    // 查询订单数据
    orderData(index) {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: this.tabIndex,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 700) {
            this.$toast.push({ name: "Login" });
          } else if (result.data.code == 70000) {
            this.$toast(result.data.msg);
            //  如果没有订单数据，移除tab
            if (this.tabIndex == 0 && result.data.result.length == 0) {
              this.isHas = false;
              return;
            }
            // 按照订单编号oid进行分类
            let orderDatas = [];
            result.data.result.map((v) => {
              for (let i = 0; i < orderDatas.length; i++) {
                if (orderDatas[i].oid == v.oid) {
                  orderDatas[i].data.push(v);
                  orderDatas[i].count += v.count;
                  orderDatas[i].total += v.count * v.price;
                  return;
                }
              }

              // 如果找不到订单数据,则初始化数据
              let o = {};
              o.oid = v.oid;
              o.count = v.count;
              o.total = v.count * v.price;
              o.status = v.status;
              o.date = v.createdAt;
              o.data = [v];
              orderDatas.push(o);
            });
            this.orderList = orderDatas;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log(err);
        });
    },

    //确认收货
    confirmReceive(item, index) {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        //跳回登录页面
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "/receive",
        data: {
          appkey: this.appkey,
          tokenString,
          oid: item.oid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            this.$router.push({ name: "Login" });
          } else if (result.data.code == 80000) {
            if (this.tabIndex == 1) {
              // this.orderData.splice(index, 1);
              this.orderList.splice(index, 1);
            }
            item.status = 2;
          }

          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //删除订单
    removeOrder(oid, index) {
      console.log("index ==> ", index);

      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        //跳回登录页面
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "/removeOrder",
        data: {
          appkey: this.appkey,
          tokenString,
          oid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            this.$router.push({ name: "Login" });
          } else if (result.data.code == 90000) {
            this.orderList.splice(index, 1);
          }

          this.$toast(result.data.msg);
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>